* {
    box-sizing: border-box;
}

body {
    background-color: #172742;
    font-family: 'Nanum Gothic', sans-serif;
    font-weight: 300;
    font-size: 18px;
    line-height: 1.6;
    max-width: 950px;
    margin: auto;
}

.login-page {
    background-color: #fff;
    margin-top: 5%;
    padding: 15px;
}

.btn {
    color: #BD2099;
    background-color: #fff;
    font-family: inherit;
    font-size: 18px;
    transition-duration: 0.5s opacity;
}

.btn-link:hover,
.btn-link:focus {
    color: #BD2099;
}

.btn-link:active {
    color: #0A8A87;
}

.btn-primary {
    border-color: #BD2099;
    border-radius: 0px;
}

.btn-primary:hover,
.btn-primary:focus {
    background-color: #BD2099;
    border-color: #BD2099;
    color: #fff;
    transition-duration: 0.5s opacity;
}

.btn-primary:active {
    background-color: #0A8A87;
    border-color: #0A8A87;
    color: #fff;
    transition-duration: 0.5s opacity;
}

.container {
    max-width: 800px;
    margin: auto;
}